import React, {useState} from 'react'
import MovieCard from './movieCard'

export default function SearchMovies() {
    const [query, setQuery] = useState("");
    const [movies, setMovies] = useState([]);

    const searchMovies = async (e) => {
        e.preventDefault();
        if(query !== ""){
        const url = `https://api.themoviedb.org/3/search/movie?api_key=d5949b3a4710bd04c0a8bc49dcbdca97&language=en-US&query=${query}&page=1&include_adult=false`;
    
        try {
            const res = await fetch(url);
            const data = await res.json();
            setMovies(data.results);
        }
        catch(err) {
            console.error(err);
            }
        }
    }

    return (
            <React.Fragment>
            <form className="form" onSubmit={searchMovies}>
                <input className="input" type="text" placeholder="The Hunger Games" value={query} onChange={(e)=> setQuery(e.target.value)}/>
                <button className="button" type="submit">Search</button>
            </form>

            <MovieCard movies={movies}/>
            </React.Fragment>
    )
}